import React, { Component } from "react"
import facade from "../apiFacade";


class LoginForm extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			username: "",
			role: "",
		}
	}
	login = (evt) => {
		evt.preventDefault();
		this.props.login(this.state.username, this.state.password);
	}

	onChange = (evt) => {
		this.setState({ [evt.target.id]: evt.target.value })
	}
	render() {
		return (
			<div>
				<form onSubmit={this.login} onChange={this.onChange} >
					<input placeholder="User Name" id="username" />
					<input placeholder="Password" id="password" />
					<button>Login</button>
				</form>
			</div>
		)
	}

}

class UserInfo extends React.Component {
	constructor(props) {
		super(props);
	}
	logout = (evt) => {
		evt.preventDefault();
		this.props.logout();
	}
	render() {
		return (
			<div>
				<p className="header">logged in as: {this.props.username}, role: {this.props.role}</p>
				<button onClick={this.logout}>Logout</button>
			</div>
		)
	}

}

class Login extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			username: "",
			role: "",
			loggedIn: false
		}
	}
	Flogout = async() => {
		
		this.setState({ loggedIn: false });
	}
	login = async (username, pass) => {
		const res = await facade.login(username, pass);
		this.setState({ loggedIn: true , username: res.username, role: res.role });
	}
	render() {
		return (
			<div>
				{ this.state.loggedIn ? (<UserInfo username={this.state.username} role={this.state.username} logout={this.Flogout} />) :
					(<LoginForm login={this.login} />)
				}
			</div>
		)
	}
}



export default Login;
